document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
} );

function iniciarApp() {
    crearGaleria();
    scrollNav();
    navegacionFija();
}

function navegacionFija() {
    const barra = document.querySelector('.header'); //Seleccionaremos el header como posición fija
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function() {
         
        if( sobreFestival.getBoundingClientRect().bottom < 0 ) { //Esa función nos da mucha info a la ubicación de un elemento
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });
}

function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion-principal a') //Leer los enlces
     
    enlaces.forEach( enlace => { //Iterar en cada enlace y asociarle un addEvenListener
    //No podemos asociar un EventListener a un resultado que tenga más de uno

    enlace.addEventListener('click', function(e) { //Escucha por un click y ejecuta la función
        e.preventDefault(); //Evita el scroll directo por default de un ID
        const seccionScroll = e.target.attributes.href.value; //Target es a lo que le hemos dado click
        const seccion = document.querySelector(seccionScroll); //Instancia a la que le vamos a dar scroll
        seccion.scrollIntoView({ behavior: 'smooth'}); //API
    })
    });
}

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes')

    for(let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture');

        imagen.innerHTML = ` 
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" 
        alt="Imagen Galeria"> 
        `;

        imagen.index = i;
        imagen.onclick = function() { //Es una función callback porque podemos pasarle parámetros
            mostrarImagen(imagen.index);
        }

        galeria.appendChild(imagen);
    }
}

function mostrarImagen(id) {
    const imagen = document.createElement('picture');
    imagen.innerHTML = ` 
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" 
        alt="Imagen Galeria"> 
        `;

        //Crea el overlay con la imagen
        const overlay = document.createElement('DIV');
        overlay.appendChild(imagen);
        overlay.classList.add('overlay'); 
        overlay.onclick = function() { //Damos click en cualquier parte y cierra la img
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
            overlay.remove();
        }

        //Botón para cerrar la ventana modal
        const cerrarModal = document.createElement('P');
        cerrarModal.textContent = 'X';
        cerrarModal.classList.add('btn-cerrar');

        cerrarModal.onclick = function() {
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
            overlay.remove();
        }
        overlay.appendChild(cerrarModal);
    

    //Añadirlo al HTML
    const body = document.querySelector('body');//Debemos mostrar las variables en pantalla para que suceda el evento
    body.appendChild(overlay);
    body.classList.add('fijar-body'); }